<template>
  <div class="headerWrap" :style="{background:col}">
	  <slot></slot>
	  <slot name="leftImg"></slot>
	  <slot name="rightImg"></slot>
	  <slot name="midImg"></slot>
  </div>
</template>

<script>
	export default{
		props:{
			col:{
				default:"#f7f7f7"
			}
		}
	}
</script>

<style>
.headerWrap{width:100%;height:48px;position:relative;top:0;left:0;}
h2{line-height:48px;font-size:22px;}
.h_left{width:20%;height:20px;border-radius:5px;}
.h_right{width:80%;height:20px;margin-left:20px;border-radius:5px;border:none;text-indent: 1em;display: block}
</style>



