<template>
	<div class="audioList">
			<ul>
			    <li><router-link to="/audio/film">电影</router-link></li>
			    <li><router-link to="/audio/read">读书</router-link></li>
			    <li><router-link to="/audio/tv">电视</router-link></li>
			    <li><router-link to="/audio/city">同城</router-link></li>
			    <li><router-link to="/audio/music">音乐</router-link></li>
			</ul>
			<router-view></router-view>
	</div>
</template>
<script>
export default{
	created:function(){
		this.$router.push("/audio/film")
	},
}
</script>
<style>
	ul{list-style:none;height:30px}
	.audioList{margin-top:10px;margin-left:0;}
	.audioList a{text-decoration:none;width:100%;color:gray;line-height:30px;height:30px;display: block}
	.audioList li{float:left;width:20%;text-align: center;}
	li .router-link-active {border-bottom:2px solid green;color:green}
</style>


