<template>
  <div class="tabbarWrap">
  	<Wrap :sel="selected">
  		<Item txt="首页" id='home' @change="getVal" changeColor="green">
  			<img src="../assets/images/ic_tab_home_normal.png" slot="normal"/>
  			<img src="../assets/images/ic_tab_home_active.png" slot="active"/>
  		</Item>
  		<Item txt="书影音" id='audio' @change="getVal" changeColor="green">
  			<img src="../assets/images/ic_tab_subject_normal.png" slot="normal"/>
  			<img src="../assets/images/ic_tab_subject_active.png" slot="active"/>
		  </Item>
  		<Item txt="广播" id='broadcase' @change="getVal" changeColor="green">
  			<img src="../assets/images/ic_tab_status_normal.png" slot="normal"/>
  			<img src="../assets/images/ic_tab_status_active.png" slot="active"/>
		  </Item>
  		<Item txt="小组" id='group' @change="getVal" changeColor="green">
  			<img src="../assets/images/ic_tab_home_normal.png" slot="normal"/>
  			<img src="../assets/images/ic_tab_home_active.png" slot="active"/>
		  </Item>
  		<Item txt="我的" id='mine' @change="getVal" changeColor="green">
  			<img src="../assets/images/ic_tab_profile_female_normal.png" slot="normal"/>
  			<img src="../assets/images/ic_tab_profile_female_active.png" slot="active"/>
		  </Item>
  	</Wrap>
  </div>
</template>

<script>
import Wrap from './wrap'
import Item from './item'
	export default{
		components:{
			Wrap,Item
		},
    data:function(){
      return {
        selected:'home'
      }
    },
    methods:{
      getVal:function(val){
        this.selected=val;
        console.log(val)
      }
    }
	}

</script>

<style>

</style>



