<template>
	<div>
		<div class="btitle">
			<div class="b_top">
				<img src="../assets/images/banner/04.jpg" class="titleimg"/>
				<h3>发现更多有趣的人</h3>
				<p>关注他们，发现更大的世界</p>
			</div>
			<div class="b_bot" v-for="(item,index) in arr">
				<div class="del" @click="del(index)">×</div>
				<div class="bottitle">
					<img src="../assets/images/banner/05.jpg" class="b_botimg"/>
					<div class="b_mid">
						<h4>{{item.name}}</h4>
						<p>5400人关注</p>
					</div>
					<button type="button" v-show='!item.showing' @click="atten(index)">+关注</button>
					<button type="button" v-show='item.showing' @click="atten(index)" style="background:#ccc">-已关注</button>
				</div>
				<p class="wen1">{{item.title}}</p>
				<p class="wen2">{{item.des}}</p>
				<div class="bcom">
					<img src="../assets/images/ic_comment_full.png" class="b_botzan"/>
					<img src="../assets/images/ic_fill_voted_large.png" class="b_botliu" @click="add(index)"/><span v-show="item.num!=0">{{item.num}}</span>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
export default{
	data:function(){
		return {
			arr:[{name:"许诺",title:"谁的青春不迷茫",des:"电脑桌上养了两株薄荷，有的叶子已近枯黄，摘下来是，仍饱含清新之气，让人惊喜。即使死了，也并非一文不值。恐怕这就是回忆的价值.“我很好”不是指你终于熬到有了钱，有了朋友，有了人照顾的日 子。",showing:false,num:0},
			{name:"方木",title:"心理罪",des:"电脑桌上养了两株薄荷，有的叶子已近枯黄，摘下来是，仍饱含清新之气，让人惊喜。即使死了，也并非一文不值。恐怕这就是回忆的价值.“我很好”不是指你终于熬到有了钱，有了朋友，有了人照顾的日 子。而是你终于可以习惯没有钱，没有朋友，没有人照顾的日子。“我很好”视告诉我们，你越来越能接受现实，而不是越来越现实。",showing:false,num:0},
			{name:"苏星宇",title:"怦然心动",des:"而是你终于可以习惯没有钱，没有朋友，没有人照顾的日子。“我很好”视告诉我们，你越来越能接受现实，而不是越来越现实。",showing:false,num:0},
			{name:"郑开司",title:"拽而有礼",des:"而是你终于可以习惯没有钱，没有朋友，没有人照顾的日子。“我很好”视告诉我们，你越来越能接受现实，而不是越来越现实。",showing:false,num:0},
			{name:"草草",title:"念念不忘",des:"而是你终于可以习惯没有钱，没有朋友，没有人照顾的日子。“我很好”视告诉我们，你越来越能接受现实，而不是越来越现实。",showing:false,num:0},
			{name:"李喋喋",title:"1987了",des:"而是你终于可以习惯没有钱，没有朋友，没有人照顾的日子。“我很好”视告诉我们，你越来越能接受现实，而不是越来越现实。",showing:false,num:0},
			],
			
		}
	},
	methods:{
		del:function(i){
			this.arr.splice(i,1)
		},
		atten:function(i){
			this.arr[i].showing=!this.arr[i].showing
			console.log(1)
		},
		add:function(i){
			this.arr[i].num++
		}
	}
}
</script>
<style>
.btitle{padding:20px 5px  5px 5px;background:#eee;}
.b_top{height:100px;width:100%}
.titleimg{width:80px;float:left;margin-left:60px}
.b_top p{float:left;margin-left:10px;margin-top:5px}
.b_top h3{float:left;margin-left:10px}

.b_bot{width:80%;border-radius:5px;background:white;padding:10px 15px 40px 15px;margin-left:20px;margin-bottom:20px}
.del{text-align:right;width:95%;height:20px;line-height:20px;font-size:16px;color:gray}

.bottitle{width:100%;height:70px}
.b_botimg{width:60px;border-radius:50%;float:left}
.b_mid{float:left;margin-left:10px}
.b_mid p,h4{text-align:left}
.bottitle button{float:right;margin-right:20px;width:80px;height:30px;background:green;border:none;border-radius:10px;color:white;font-size:14px}

.wen1{font-size:18px;height:20px;line-height:20px;margin-right:20px;margin-left:50px;text-align:left;}
.wen2{font-size:14px;line-height:20px;margin-left:20px;text-align:left;background:black;color:white;margin-right:20px;margin-top:10px;padding:10px}

.bcom{float:left;margin-left:25px;margin-top:5px}
.b_botzan{width:20px;}
.b_botliu{width:30px;}
.bcom span{font-size:14px;line-height:12px;color:#ccc}
</style>


