<template>
  <div class="itemWrap" @click="fn">
  	<span v-show="bol"><slot name="normal"></slot><br></span>
  	<span v-show="!bol"><slot name="active"></slot><br></span>
  	<span v-text="txt" v-show="bol"></span>
  	<span v-text="txt" v-show="!bol" :style="{color:changeColor}" ></span>
  </div>
</template>

<script>
export default{
	props:['txt','id',"changeColor"],
	computed:{
		bol:function(){
			if(this.$parent.sel==this.id){
				return false
			}
			return true
		}
	},
	methods:{
		fn:function(){
			this.$router.push('/'+this.id)
			this.$emit('change',this.id)
		}
	}
}
</script>

<style>
.itemWrap{width:20%;float:left;font-size:16px}
.itemWrap img{width:30px;}
</style>



