<template>
	<div>
		<Myheader>
			<h2>书影音</h2>
			<img src="../../assets/images/ic_actionbar_search_icon.png" slot="leftImg" class="amid" />
			<img src="../../assets/images/ic_create_group_chat_blue.png" slot="midImg" class="aright"/>
		</Myheader>
		<AudioList></AudioList>
		<div class="bottomline"></div>

	</div>
</template>
<script>
import Myheader from '../../components/header'
import AudioList from '../../components/audioList'
export default{
	components:{
		Myheader,
		AudioList
	},
  created:function(){
  	this.$router.push("/audio/film")
  },
}
</script>
<style>
	ul{list-style: none}
	.audioList a{text-decoration: none;}
	.audioList li{float:left;width:20%;}
	 .amid{left:275px;top:15px;width:25px;position:absolute}
	 .aright{right:10px;top:10px;width:30px;position:absolute}
 </style>


