<template>
	<div class="swiper-container" :class="swiperid">
		<div class="swiper-wrapper">
			<slot name="slotcont"></slot>
		</div>
		<div :class="{'swiper-pagination':'pagination'}" :style="{'text-align':'paginationDirection'}"></div>
	</div>
</template>
<script>
import '../assets/lips/swiper/js/swiper.js'
export default{
	props:{
		swiperid:{
			type:String,
			dafault:""
		},
		pagination:{
			type:Boolean,
			dafault:true
		},
		paginationDirection:{
			type:String,
			dafault:"center"
		},
		effect:{
			type:String,
			dafault:'slide'
		},
		loop:{
			type:Boolean,
			default:true
		},
		autoplay:{
			type:Number,
			dafault:'1000'
		},
		direction:{
			type:String,
			dafault:'horizontal'
		},
		paginationType:{
			type:String,
			dafault:'bullets'
		}
	},
	mounted:function(){
		var that=this;
		new Swiper('.'+that.swiperid,{
			pagination:'.swiper-pagination',
			paginationDirection:that.paginationDirection,
			loop:that.loop,
			autoplay:that.autoplay,
			effect:that.effect,
			direction:that.direction,
			paginationType:that.paginationType,
		})
	}
}
</script>
<style>
	@import '../assets/lips/swiper/css/swiper.css';
	.swiper-container img{width:100%}
	.swiper-pagination-bullet-active{background:white;}
</style>


